// 指令滚动配置信息缓存集合
const scrollOptionMap = {};

/**
 * @description 滚动配置创建工厂
 * @method create 创建配置文件
 */
const scrollOptionFactory = {
    /**
     * @description 创建滚动配置信息
     * @param {Object} context 滚动回调事件绑定的上下文
     * @param {Object} options 可配置信息项
     * @param {Object} eventHandlers 滚动回调接口配置项
     */
    create(context, options, eventHandlers) {
        let opt = {
            timer: null,
            scrollLeftLast: 0,
            scrollTopLast: 0,
            edgeTouched: false,
            eventHandlerContext: context || this,
            options: {
                eventTriggerInterval: 80,   // 事件触发延迟时间(ms)
                edgeTouchPrecision: 2,      // 边界接触判读精度(px)
                edgeTouchOnce: false,       // 是否只触发一次边界事件
                edgeTouchDirection: null    // 边界事件触发方向,为空时任意方向都触发,可选值 [left, right, up, down, vertical, horizontal]
            },
            eventHandlers: {
                // 滚动触边事件
                touchEdge(direction) {
                    console.log('touchEdge', direction)
                }
            },
            /**
             * @description 处理滚动事件,触发一些指定的滚动事件
             * @param {Object} scrollMeasure 滚动过程中的一些测量值
             * @param {Object} scrollResult 滚动结束的计算结果值
             */
            handleScroll(scrollMeasure, scrollResult) {
                // 添加一个定时器作事件延迟,避免短时间内重复触发,造成性能浪费
                // 但由于touchEdge事件触发频率比较低,未做拦截,确保每次都能适当的触发
                if (this.timer && !scrollResult.touchEdge) return;

                clearTimeout(this.timer);
                this.timer = setTimeout(() => {
                    // event -> touchEdge
                    if (scrollResult.touchEdge && (this.options.edgeTouchOnce === false || (this.options.edgeTouchOnce === true && this.edgeTouched === false))) {
                        if (this.checkTouchEdgeCondition(scrollResult.direction) && typeof(this.eventHandlers.touchEdge) === 'function') {
                            this.edgeTouched = true;
                            this.eventHandlers.touchEdge.call(this.eventHandlerContext, scrollResult.direction);
                        }
                    }

                    this.timer = null;
                }, this.options.eventTriggerInterval);
            },
            // 判断是否需要触发触边事件
            checkTouchEdgeCondition(direction) {
                if (!direction) return false;
                if (!this.options.edgeTouchDirection) return true;
                switch (this.options.edgeTouchDirection) {
                    case 'horizontal': return (direction === 'left' || direction === 'right');
                    case 'vertical': return (direction === 'up' || direction === 'down');
                    case 'left': return (direction === 'left');
                    case 'right': return (direction === 'right');
                    case 'up': return (direction === 'up');
                    case 'down': return (direction === 'down');
                    default: return false;
                }
            },
            // 原生js scroll事件回调方法
            scrollHandler(e) {
                let horDirection = '', verDirection = '', direction = '';
                let touchEdge = false;
        
                if (e.target.scrollLeft - this.scrollLeftLast > 0) {
                    horDirection = 'right';
                } else if (e.target.scrollLeft - this.scrollLeftLast < 0) {
                    horDirection = 'left';
                } else {
                    horDirection = '';
                }
        
                if (e.target.scrollTop - this.scrollTopLast > 0) {
                    verDirection = 'down';
                } else if (e.target.scrollTop - this.scrollTopLast < 0) {
                    verDirection = 'up';
                } else {
                    verDirection = '';
                }

                direction = verDirection || horDirection; // 以垂直滚动方向优先判定
                
                // 容器内容滚动触边判定逻辑
                switch (direction) {
                    case 'left':
                        touchEdge = (e.target.scrollLeft < this.options.edgeTouchPrecision);
                        break;
                    
                    case 'right':
                        touchEdge = (Math.abs(e.target.scrollWidth - e.target.offsetWidth - e.target.scrollLeft)  < this.options.edgeTouchPrecision);
                        break;

                    case 'up':
                        touchEdge = (e.target.scrollTop < this.options.edgeTouchPrecision);
                        break;

                    case 'down':
                        touchEdge = (Math.abs(e.target.scrollHeight - e.target.offsetHeight - e.target.scrollTop)  < this.options.edgeTouchPrecision);
                        break;

                    default:
                        touchEdge = false;
                        break;
                }
        
                this.scrollLeftLast = e.target.scrollLeft;
                this.scrollTopLast = e.target.scrollTop;

                this.handleScroll({
                    scrollLeft: e.target.scrollLeft,
                    scrollTop: e.target.scrollTop,
                    scrollWidth: e.target.scrollWidth,
                    scrollHeight: e.target.scrollHeight,
                    offsetWidth: e.target.offsetWidth,
                    offsetHeight: e.target.offsetHeight
                }, {
                    direction: direction,
                    touchEdge: touchEdge
                });
            }
        };

        if (options) opt.options = Object.assign({}, opt.options, options);
        if (eventHandlers) opt.eventHandlers = Object.assign({}, opt.eventHandlers, eventHandlers);

        for (let key in opt) {
            if (typeof(opt[key]) === 'function') {
                opt[key] = opt[key].bind(opt);
            }
        }

        return opt;
    } 
}; 

// 滚动指令支持的方向参数
const srcollMonitorDirections = ['left', 'right', 'up', 'down', 'vertical', 'horizontal'];


/**
 * @description 滚动监听指令
 * 
 * 指令： v-scroll
 * 
 * 参数说明:
 * ======================================================================================================
 *  left, right, up, down, vertical, horizontal 
 *      用法 -> v-scroll:left
 *      说明 -> 设置绑定元素监听的滚动方向，vertical代表垂直方向,包含up和down; horizontal水平方向包含left和right
 * 
 * 不传默认为 '所有方向'
 * 
 * 
 * 绑定值说明：   
 * ======================================================================================================
 *    绑定值类型：    参数含义：
 *      Function       作为eventHandlers的touchEdge方法合并到配置项
 *      Object         对象作为eventHandlers合并到配置项
 * 
 * 
 * 修饰符说明:
 * ======================================================================================================
 *  once
 *      用法 -> v-scroll.once
 *      说明 -> 若添加此修饰,所有事件将会只触发一次
 * 
 * 
 * options属性说明:
 * ======================================================================================================
 * 
 *      无
 * 
 */
const Scroll = {
    name: 'scroll',
    hook: {
        bind(el, binding, vnode) {
            const context = vnode.context;

            let eventOptions = {
                edgeTouchDirection: srcollMonitorDirections.indexOf(binding.arg) > -1 ? binding.arg : null,
                edgeTouchOnce: Boolean(binding.modifiers['once'])
            };

            let eventHandlers = null;
            if (typeof(binding.value) == 'function') {
                eventHandlers = { touchEdge: binding.value };
    
            } else if (typeof(binding.value) == 'object') {
                eventHandlers = binding.value;
    
            } else {
                // do nothing
            }
            
            let scrollTag = 'ST_' + new Date().getTime();
            let scrollOption = scrollOptionFactory.create(context, eventOptions, eventHandlers);
            el.dataset.scrolltag = scrollTag;
            el.addEventListener('scroll', scrollOption.scrollHandler);
            scrollOptionMap[scrollTag] = scrollOption;
        },
        unbind(el, binding, vnode) {
            let scrollOption = scrollOptionMap[el.dataset.scrolltag];
            if (scrollOption) {
                el.removeEventListener('scroll', scrollOption.scrollHandler);
                delete scrollOptionMap[el.dataset.scrolltag];
            }
        }
    }
};

export default Scroll;


