/**
 * @description [2019-11-20] - VUE全局自定义指令配置
 *
 * @author KKPE
 * @version v1.0.0
 *
 */

import { constantVar } from '../configs/constantVar.js';

const install = function (Vue) {
    /**
     * @description 组件触发用户信息显示指令
     *
     * 指令： v-bizcard
     *
     * 参数说明:
     * ======================================================================================================
     *  disabled
     *      用法 -> v-bizcard:disabled
     *      说明 -> 是否禁用，当作为动态参数时，将会计算表达式结果并转为Boolean值
     *
     *
     *
     * 绑定值说明：
     * ======================================================================================================
     *    绑定值类型：    参数含义：
     *      String         作为作为userId传到BizCard组件
     */
    Vue.directive('bizcard', (el, binding, vnode) => {
        let disabled = Boolean(binding.arg);

        if (disabled) {
            el.setAttribute('title', '');
            el.onmouseover = undefined;
            el.onmouseout = undefined;
            el.onclick = undefined;
        } else {
            let userId = binding.value,
                userType = constantVar.VAR_USER_TYPE_CUSTOMER;
            if (binding.modifiers.staff)
                userType = constantVar.VAR_USER_TYPE_STAFF;

            el.setAttribute('title', '点击查看用户信息');
            el.onmouseover = () => {
                el.classList.add('info-ext-hover');
            };
            el.onmouseout = () => {
                el.classList.remove('info-ext-hover');
            };
            el.onclick = () => {
                window.$BizCard.display({ userId: userId, userType: userType });
            };
        }
    });
};

export default { install };



