 

/**
 * @description 授权配置定义
 * 
 */
const authDefinition = {
    resId: '',          // 资源id
    staticAuth: false,  // 静态授权标记（会根据资源id读取store中permission配置项或者从后台读取）
    dynamicAuth: false, // 动态授权标记（由绑定组件内部自己设定校验逻辑）
    authorize: (userInfo) => true, // 动态权限授权回调函数,需要返回true或false值, userInfo当前登陆用户信息
    afterAuth: (pass, userInfo) => {}, // 授权验证后回调函数或者Boolean值， pass是否授权通过, userInfo当前登陆用户信息
    data: null  // 额外的配置数据
};


/**
 * @description 验证授权是否通过
 * 
 * @param {VueComponent} context 指令绑定组件的上下文
 * @param {Object} authValue 权限用户配置值
 * @param {Object} authOptions 授权指令配置项
 * 
 */
const checkPermission = async function(context, authValue, authOptions) {
    let authConfig = Object.assign({}, authDefinition, authValue);
    let data = { result: false, authConfig: authConfig };

    if (!authConfig) return data;

    if (typeof(authConfig.resId) === 'boolean') {
        authConfig.staticAuth = authConfig.resId;

    } else if (!authConfig.resId) { 
        authConfig.staticAuth = true; // 不存在资源id时，默认通过静态授权

    } else {
        // 查询授权信息结果,外部可以实现从vuex中查询或后台等,但需要配置options的verify回调函数
        if (context && authOptions && typeof(authOptions.verify) === 'function') {
            try {
                authConfig.staticAuth = await authOptions.verify(context, authConfig.resId);
            } catch (e) {
                console.log('[authorize error]: ', e);
                authConfig.staticAuth = false;
            }
            
        } else {
            console.info('can not find the store instance');
            authConfig.staticAuth = false;
        }
    }


    if (typeof(authConfig.authorize) == 'function') {
        let userInfo = null;
        if (authOptions && typeof(authOptions.userInfo) == 'function') {
            userInfo = authOptions.userInfo();
        }
        authConfig.dynamicAuth = authConfig.authorize.call(context, userInfo);

    } else if (typeof(authConfig.authorize) == 'boolean') {
        authConfig.dynamicAuth = authConfig.authorize;

    } else {
        authConfig.dynamicAuth = true; 
    }

    data.result = (authConfig.staticAuth && authConfig.dynamicAuth);
    data.authConfig = authConfig;

    return data;
}

// 授权结果处理方法
const authHandler = {
    // 修改组件可见性，将会从dom中移除
    handleVisible(hasPermission, authConfig, { context, el, binding, vnode, oldVnode }) {
        if (!hasPermission) {
            // 非update或componentUpdated条件下不能执行移除dom树，不然会引发异常
            if (!oldVnode.elm) { 
                el.style.display = 'none';
                return;
            }

            el.style.display = '';
            let comment = document.createComment('');
            Object.defineProperty(comment, 'setAttribute', { value: () => undefined });
            vnode.elm = comment;
            vnode.text = '';
            vnode.isComment = true;
            vnode.context = undefined;
            vnode.tag = undefined;
            vnode.data.directives = undefined;
            if (vnode.componentInstance) vnode.componentInstance.$el = comment;
            if (el.parentNode) el.parentNode.replaceChild(comment, el);
            // if (el.parentNode) el.parentNode.removeChild(el);

        } else {
            el.style.display = '';
        }
    },
    // 仅修改元素的disabled属性（支持自定义组件在props中暴露的disabled）
    handleEnable(hasPermission, authConfig, { context, el, binding, vnode, oldVnode }) {
        if (vnode.componentInstance) { // vue组件, 目前有bug,直接修改props属性会抛出vue异常警告,但不影响功能
            if (!hasPermission) {
                // vnode.componentOptions.propsData['disabled'] = true;
                vnode.context.$set(vnode.componentInstance, 'disabled', true);
                // vnode.componentInstance.$forceUpdate();
            } else {
                // vnode.componentOptions.propsData['disabled'] = false;
                vnode.context.$set(vnode.componentInstance, 'disabled', false);
                // vnode.componentInstance.$forceUpdate();
            }

        } else { // 非vue组件
            if (!hasPermission) {
                el.setAttribute('disabled', 'disabled');
            } else {
                el.removeAttribute('disabled');
            }
        }
    },
    // 修改元素绑定的class
    handleAlterClass(hasPermission, authConfig, { context, el, binding, vnode, oldVnode }) {
        if (authConfig.data) {
            if (hasPermission) {
                el.classList.add(authConfig.data);

            } else {
                el.classList.remove(authConfig.data);
            }
        }
    }
};


/**
 * @description 组件授权验证指令
 * 
 * 指令： v-auth
 * 
 * 参数说明:
 * ======================================================================================================
 *  visible 
 *      用法 -> v-auth:visible
 *      说明 -> 设置绑定元素的可见性，当不可见时将会从dom中移除
 * 
 *  enable 
 *      用法 -> v-auth:enable
 *      说明 -> 设置绑定元素的disabled属性（支持原生html元素和支持【disabled】props属性的vue组件）
 * 
 *  class 
 *      用法 -> v-auth:class
 *      说明 -> 设置绑定元素的class，授权通过添加class，反之移除
 * 
 * 不传默认为 visible
 * 
 * 
 * 绑定值说明：   
 * ======================================================================================================
 *    绑定值类型：    参数含义：
 *      String         作为 [resId] 合并到authDefinition
 *      Boolean        作为 [authorize] 合并到authDefinition
 *      Function       作为 [authorize] 合并到authDefinition， （authorize函数传递参数详见上面【authDefinition】中说明）
 *      Object         对象属性为 [ resId、authorize、afterAuth、data ]的，将会合并到authDefinition
 * 
 * 
 * options属性说明:
 * ======================================================================================================
 * 
 *      verify: function(context, resId)   资源授权验证函数,参数[context]当前绑定指令组件所在上下文,[resId]绑定资源id,函数需要返回Boolean值或Promise对象
 *      userInfo: function() 获取用户信息函数,暂不支持异步操作,需立即返回数据
 * 
 */
const Auth = {
    name: 'auth',
    hook(el, binding, vnode, oldVnode) {
        const context = vnode.context;
        let argument = binding.arg || 'visible';
        let authValue = {};

        if (typeof(binding.value) == 'string') {
            authValue.resId = binding.value;

        } else if (typeof(binding.value) == 'boolean') {
            authValue.authorize = binding.value;

        } else if (typeof(binding.value) == 'function') {
            authValue.authorize = binding.value;

        } else if (typeof(binding.value) == 'object') {
            authValue.resId = binding.value.resId;
            authValue.authorize = binding.value.authorize;
            authValue.afterAuth = binding.value.afterAuth;
            authValue.data = binding.value.data;

        } else {
            // do nothing
        }

        checkPermission(context, authValue, this.options).then(data => {
            let directiveParams = { context, el, binding, vnode, oldVnode };
            switch (argument) {
                case 'visible': authHandler.handleVisible(data.result, data.authConfig, directiveParams); break;
                case 'enable': authHandler.handleEnable(data.result, data.authConfig, directiveParams); break;
                case 'class': authHandler.handleAlterClass(data.result, data.authConfig, directiveParams); break;
                default: break;
            }

            if (data.authConfig && typeof(data.authConfig.afterAuth) === 'function' && context) {
                let userInfo = null;
                if (this.options && typeof(this.options.userInfo) == 'function') {
                    userInfo = this.options.userInfo();
                }
                data.authConfig.afterAuth.call(context, data.result, userInfo);
            }
        });
    },
    init(Vue) {
        Vue.mixin({
            computed: {
                $PMSN() { return this.$store.state.permissions; }
            },
        });
    }
};

export default Auth;


