/**
 * @description [2019-10-21] - 一些表单常规校验方法的封装 (基本element-ui得form进行封装得)
 * 
 * @author KKPE
 * @version v1.0.0
 */

const regExpMap = {
    phone: /^1(3|4|5|6|7|8|9)\d{9}$/,
    // email: /^[a-zA-Z0-9_-]+@([a-zA-Z0-9]+\.)+(com|cn|net|org)$/,
    email: /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/
};


const validateUtils = {
    validatePhone(rule, value, callback) {
        if (rule.required !== true && !value) {
            callback();
            return;
        }

        if (!(regExpMap.phone.test(value))) {
            callback(new Error('请输入正确的手机号！'));

        } else {
            callback();
        }        
    },
    validateEmail(rule, value, callback) {
        if (rule.required !== true && !value) {
            callback();
            return;
        }

    　　if(!regExpMap.email.test(value)){
    　　　　callback(new Error('请输入正确的邮箱地址！'));

    　　} else{
    　　　　callback();
        }
    }
}

const install = function (Vue) {
    Vue.prototype.$validateUtils = validateUtils;
};

export default { install };


