import E from 'wangeditor';

const utils = {
    formatNumber(value) {
        let val = parseFloat(value);
        if (!val || isNaN(val)) val = 0;
        return val;
    }
};

const REGS = {
    placeholder:  /\{placeholder=.*?\}/gi
};

const editorHandler = {
    handleText(context, el, arg, modifier, content, expression) {
        let placeholderVal = '<空>', changeByEditor = false;;
        if (modifier.placeholder && content) {            
            let value = content.match(REGS.placeholder);
            placeholderVal = value && value.length > 0 ? value[0] : '';
            if (placeholderVal) {
                placeholderVal = placeholderVal.substring(
                    placeholderVal.indexOf('=') + 1, placeholderVal.length - 1);
            }
            content = content.replace(REGS.placeholder, '');
        }

        el.setAttribute('contenteditable', true);
        el.style.cursor = 'text';
        el.style.outline = 'none';
        el.style.boxSizing = 'border-box';
        el.style.borderWidth = '1px 1px 1px 3px';
        el.style.borderStyle = 'dashed dashed dashed solid';
        el.style.borderColor = 'transparent';        
        el.title = '点击进行编辑';                
        el.innerHTML = Boolean(content != null) 
            ? (modifier.number ? utils.formatNumber(content) : content.toString()) 
            : '';
            
        el.onclick = (e) => {
            e.stopPropagation();
            return false;
        };
        el.onmouseenter = (e) => {
            e.target.style.borderColor = 'rgba(10, 10, 10, 0.2)';
            e.target.style.backgroundColor = 'rgba(230, 230, 230, 0.2)';
        };
        el.onmouseleave = (e) => {
            if (document.activeElement != el) {
                e.target.style.borderColor = 'transparent';
                e.target.style.backgroundColor = '';
            }                        
        };
        el.onfocus = (e) => {             
            let range;            
            if (document.body.createTextRange) {
                range = document.body.createTextRange();
                range.moveToElementText(e.target);
                range.select();

            } else if (window.getSelection) {
                let selection = window.getSelection();
                range = document.createRange();
                range.selectNodeContents(e.target);
                selection.removeAllRanges();
                selection.addRange(range);
                /*if(selection.setBaseAndExtent){
                    selection.setBaseAndExtent(text, 0, text, 1);
                }*/
            }
            
            e.target.style.borderColor = 'rgba(10, 10, 10, 0.2)';
            e.target.style.backgroundColor = 'rgba(230, 230, 230, 0.2)';
        };
        el.onblur = (e) => {
            e.target.style.borderColor = 'transparent';
            e.target.style.backgroundColor = '';
            // if (!context.hasOwnProperty(binding.expression)) return;

            changeByEditor = true;
            if (modifier.number) {
                let val = utils.formatNumber(el.innerHTML);
                el.innerHTML = val;
                eval(`context.${expression}=val`);                        
                
            } else {                                                 
                if (!el.innerHTML && placeholderVal) {                        
                    el.innerHTML = placeholderVal;
                    eval(`context.${expression}=''`);                            

                } else {                            
                    eval(`context.${expression}=el.innerHTML`);                            
                }
            }  
        };
        el.onkeydown = (e) => {                    
            if (e && ((e.keyCode == 13 && !modifier.enter) || e.keyCode == 27)) {
                el.blur(); 
            }
        };

        // 对富文本组件绑定的对象属性进行监听，当修改时触发相应的更新方法                
        context.$watch(expression, val => {                    
            if (!changeByEditor) {
                el.innerHTML = Boolean(content != null) 
                    ? (modifier.number ? utils.formatNumber(val) : val.toString()) 
                    : '';
                changeByEditor = false;
            }
        });
    },
    handleHtml(context, el, arg, modifier, content, expression) {
        let editorMenuEl = document.createElement('div');
        editorMenuEl.style.cssText = 'background-color:#F2F6FC;';                

        let editorTextEl = document.createElement('div');                
        editorTextEl.style.cssText = 'height:500px';
        
        el.style.cssText = 'border: 1px solid #E4E7ED';
        el.innerHTML = '';
        el.appendChild(editorMenuEl);
        el.appendChild(editorTextEl);

        let changeByEditor = false;
        let editor = new E(editorMenuEl, editorTextEl);                
        editor.customConfig.onchange = html => {                                             
            changeByEditor = true;
            eval(`context.${expression}='${html}'`);
        };
        // editor.customConfig.uploadImgServer = '/file/add';
        editor.customConfig.customUploadImg = (files, insert) => {
            context.$pmsApis.uploadPicture({ files: files }).then(data => {                
                if (data.code === 0 && data.data instanceof Array) {
                    data.data.forEach(item => { insert(item.fileLink); });
                } else {
                    context.$message({ type: 'error', message: '图片插入失败！' });
                }
            });            
        }
        // 菜单配置
        editor.customConfig.menus = [
            'head', // 标题
            'bold', // 粗体
            'fontSize', // 字号
            'fontName', // 字体
            'italic', // 斜体
            'underline', // 下划线
            'strikeThrough', // 删除线
            'foreColor', // 文字颜色
            'backColor', // 背景颜色
            'link', // 插入链接
            'list', // 列表
            'justify', // 对齐方式
            'quote', // 引用
            'emoticon', // 表情
            'image', // 插入图片
            'table', // 表格
            'code', // 插入代码
            'undo', // 撤销
            'redo' // 重复
        ];
        editor.create(); // 创建富文本实例                
        editor.txt.html(content);

        // 对富文本组件绑定的对象属性进行监听，当修改时触发相应的更新方法                
        context.$watch(expression, val => {                    
            if (!changeByEditor) {                        
                editor.txt.html(val);
                changeByEditor = false;
            }
        });
    }
};

/**
 * @description 使dom元素直接支持编辑的指令
 * 
 * 指令： v-editable
 * 
 * 参数说明:
 * ======================================================================================================
 *  html 
 *      用法 -> v-editable:html
 *      说明 -> 设置绑定元素渲染的内容为html文档，否则默认以纯文本渲染
 * 
 *  rich 
 *      用法 -> v-editable:rich
 *      说明 -> 设置绑定元素渲染为富文本编辑器
 * 
 *  disable 
 *      用法 -> v-editable:disable
 *      说明 -> 设置绑定元素是否禁用编辑
 * 
 * 不传默认为空
 * 
 * 
 * 修饰符说明:
 * ======================================================================================================
 * 
 *  enter
 *      用法 -> v-editable.enter
 *      说明 -> 若添加此修饰，支持按回车换行（非富文本模式有用）
 * 
 *  number
 *      用法 -> v-editable.number
 *      说明 -> 若添加此修饰，输入内容进行格式校验，只支持数值类型输入（仅纯文本模式有用）
 * 
 *  placeholder
 *      用法 -> v-editable.placeholder
 *      说明 -> 若添加此修饰，第一次加载时将会解析绑定值中的{placeholder=xxx}内容为placeholder（仅纯文本模式有用）
 * 
 * 绑定值说明：   
 * ======================================================================================================
 *    绑定值类型：    参数含义：
 *      String         --
 *      Boolean        --
 *      Function       --
 *      Object         --
 * 
 * 
 */
const Editable = {
    name: 'editable',
    hook: { 
        bind: (el, binding, vnode) => {            
            const context = vnode.context;            
            let modifier = {
                enter: Boolean(binding.modifiers['enter']),
                number: Boolean(binding.modifiers['number']),
                placeholder: Boolean(binding.modifiers['placeholder'])
            };

            if (!context) return;

            switch (binding.arg) {
                case 'disable': 
                    el.setAttribute('contenteditable', false);
                    break;
                
                case 'rich': 
                    editorHandler.handleHtml(context, el, binding.arg, modifier, binding.value, binding.expression);
                    break;
                
                default: 
                    editorHandler.handleText(context, el, binding.arg, modifier, binding.value, binding.expression);
                    break;
            }
        }
    }    
};

export default Editable;


