/**
 * 
 * @module directive vue全局指令集
 * @description [2019-11-29] - VUE全局自定义指令配置
 * @author KKPE
 * @version v1.0.0
 *
 */

import Auth from './auth.js';
import Scroll from './scroll.js';
import Request from './request.js';
import Editable from './editable.js';
import Drag from './drag.js';


const directives = [ 
    Auth, 
    Scroll, 
    Request, 
    Editable,
    Drag 
];


/**
 * @param {Object} options 指令全局配置信息 (options的属性名需要设置为以上定义的指令名[directive.name]才能被正确配置)
 * 
 */
const install = function (Vue, options) {
    directives.map(directive => {
        directive.options = options ? options[directive.name] : null; // 自动注入配置信息

        // 绑定hook函数的this指向,否则无法获取自动注入的options项
        if (typeof(directive.hook) === 'function') {
            directive.hook = directive.hook.bind(directive);

        } else if (typeof(directive.hook) === 'object') {
            for (let key in directive.hook) {
                if (typeof(directive.hook[key] === 'function')) {
                    directive.hook[key] = directive.hook[key].bind(directive);
                }
            }
        }

        // 调用指令初始化函数
        if (typeof(directive.init) === 'function') {
            directive.init(Vue);
        }

        return Vue.directive(directive.name, directive.hook);
    });
};

export default { install };


