import sessionUtils from '../data/session/sessionUtils.js';

/**
 * @description 全局注入指令的配置文件
 *
 */

const directiveOptions = {
    // v-auth指令
    auth: {
        // 返回Boolean值或Promise对象, Promise的resolve的参数应该为Boolean值
        verify(context, resId) {
            // 现行方案从vuex中读取
            // return context.$store.getters.findPermission(resId);
            return '';
        },
        // 获取登陆用户信息,暂不支持异步查询
        userInfo() {
            return sessionUtils.findUserInfo();
        },
    },
};

export default directiveOptions;



